const x = localStorage.getItem("x"); //æ­¤æ—¶å–å‡º x è¿˜æ˜¯ä¸€ä¸ªå¯¹è±¡
const xObject = JSON.parse(x); //æŠŠå­—ç¬¦ä¸²å˜æˆå¯¹è±¡ parseä¸èƒ½è®¿é—®ç©ºå­—ç¬¦ä¸²
//åˆ›å»ºå“ˆå¸Œæ•°ç»„è¡¨
const hashMap = xObject || [
  //xObjectå­˜åœ¨å°±ç”¨xï¼Œå¦åˆ™å°±ç”¨åˆå§‹â†“â†“
  { logo: "B", url: "https://www.bilibili.com" },
  { logo: "I", url: "https://www.iqiyi.com" },
  { logo: "J", url: "https://jirengu.com" },
];

const removeX = (url) => {
  return url
    .replace("https://", "")
    .replace("http://", "")
    .replace("www.", "")
    .replace(/\/.*/, ""); // \æ˜¯ä¸ºäº†è½¬æ„ åˆ é™¤ / å¼€å¤´çš„å†…å®¹
};

const render = () => {
  $ulList.find("li:not(.lastLi)").remove();
  hashMap.forEach((node, index) => {
    console.log(index);
    const $li = $(`  <li>   
          <div class="site">
            <div class="logo"> ${node.logo[0]}</div>
            <div class="link">${removeX(node.url)}</div>
            <div class='close'> 
            <svg class="icon" aria-hidden="true">
            <use xlink:href="#icon-close"></use>
          </svg></div>
          </div>
        </li> 
          `).insertBefore($lastLi);

    $li.on("click", () => {
      window.open(node.url); //ç›‘å¬LiæŒ‰é’®ï¼Œå¦‚æœç‚¹å‡»liå°±æ‰“å¼€æ–°æ ‡ç­¾
    });
    $li.on("click", ".close", (e) => {
      console.log("è¿™é‡Œ");
      e.stopPropagation(); //é˜»æ­¢å†’æ³¡
      console.log(hashMap);
      hashMap.splice(index, 1); //åœ¨indexä½ç½®åˆ é™¤1é¡¹
      render();
    });
  });
};

